@import "../../scss/_mixin.scss";

.container {
  width: 100%;
  height: 2000px;
  position: relative;
  .animationContainer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    &.block {
      position: absolute;
    }

    .flexContainer {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;

      .light {
        position: absolute;
        background-color: #0f0;
        border-radius: 50%;
        animation: lightColor 4s linear infinite, lightSize 5s linear infinite;
      }

      .cubeContainer {
        position: absolute;
        top: -50vh;
        left: -50vw;
        .row {
          display: inline-flex;
          margin-top: -3.2rem;
          margin-left: -5rem;

          &:nth-child(even) {
            margin-left: 0.2rem;
          }

          .hexagon {
            width: 10rem;
            height: 11rem;
            background-color: #000;
            clip-path: polygon(
              50% 0%,
              100% 25%,
              100% 75%,
              50% 100%,
              0% 75%,
              0% 25%
            );
            margin: 0.2rem;
          }
        }
      }

      .computer {
        position: absolute;
        width: 12rem;
        max-height: 12rem;
      }
      .screen {
        position: absolute;
        display: flex;
        flex-wrap: wrap;
        top: -5rem;
        left: -5.4rem;
        width: 11.2rem;
        height: 6.7rem;
        padding: 0;
        .code {
          position: absolute;
          font-size: 0.85rem;
          margin-right: 0.5rem;
          padding-bottom: 0.37rem;
          font-family: Monospace;
          color: #14c18b;

          &:nth-child(1) {
            @include scrollAnimate(1);
          }
          &:nth-child(2) {
            @include scrollAnimate(2);
          }
          &:nth-child(3) {
            @include scrollAnimate(3);
          }
          &:nth-child(4) {
            @include scrollAnimate(4);
          }
          &:nth-child(5) {
            @include scrollAnimate(5);
          }
          &:nth-child(6) {
            @include scrollAnimate(6);
          }
          &:nth-child(7) {
            @include scrollAnimate(7);
          }
          &:nth-child(8) {
            @include scrollAnimate(8);
          }
          &:nth-child(9) {
            @include scrollAnimate(9);
          }
          &:nth-child(10) {
            @include scrollAnimate(10);
          }
          &:nth-child(11) {
            @include scrollAnimate(11);
          }
          &:nth-child(12) {
            @include scrollAnimate(12);
          }
          &:nth-child(13) {
            @include scrollAnimate(13);
          }
          &:nth-child(14) {
            @include scrollAnimate(14);
          }
          &:nth-child(15) {
            @include scrollAnimate(15);
          }
        }
      }
    }
  }
  @keyframes lightColor {
    0% {
      filter: hue-rotate(0deg) blur(50px);
    }
    100% {
      filter: hue-rotate(360deg) blur(50px);
    }
  }

  @keyframes lightSize {
    0%,
    100% {
      width: 10rem;
      height: 10rem;
    }
    50% {
      width: 25rem;
      height: 25rem;
    }
  }
  @keyframes fadeIn1 {
    0% {
      @include fontTranslate(-20, 100, 15);
    }
    16% {
      @include fontTranslate(-23, 8, 2.8);
    }
    20%,
    100% {
      @include fontTranslate(0, 0);
    }
  }
  @keyframes fadeIn2 {
    0% {
      @include fontTranslate(8, 100, 13);
    }
    24%,
    100% {
      @include fontTranslate(3.05, 0);
    }
  }
  @keyframes fadeIn3 {
    0%,
    20% {
      @include fontTranslate(100, -100, 10);
    }
    36% {
      @include fontTranslate(28, -18, 5);
    }
    40%,
    100% {
      @include fontTranslate(9.16, 0);
    }
  }
  @keyframes fadeIn4 {
    0%,
    12% {
      @include fontTranslate(110, -10, 5);
    }
    39%,
    100% {
      @include fontTranslate(0, 1.35);
    }
  }
  @keyframes fadeIn5 {
    0%,
    8% {
      @include fontTranslate(100, -10, 30);
    }
    36% {
      @include fontTranslate(10, -10, 20);
    }
    40%,
    100% {
      @include fontTranslate(5.6, 1.35);
    }
  }
  @keyframes fadeIn6 {
    0%,
    24% {
      @include fontTranslate(-120, 20, 20);
    }
    36% {
      @include fontTranslate(-42, -10, 10);
    }
    40% {
      @include fontTranslate(-30, -9, 10);
    }
    42%,
    100% {
      @include fontTranslate(9.67, 1.35);
    }
  }
  @keyframes fadeIn7 {
    0%,
    28% {
      @include fontTranslate(100, 60, 15);
    }
    46%,
    100% {
      @include fontTranslate(0, 2.69);
    }
  }
  @keyframes fadeIn8 {
    0%,
    30% {
      @include fontTranslate(-100, 60, 15);
    }
    46% {
      @include fontTranslate(-8, 25, 6);
    }
    50%,
    100% {
      @include fontTranslate(3.56, 2.69);
    }
  }
  @keyframes fadeIn9 {
    0%,
    30% {
      @include fontTranslate(100, -100, 15);
    }
    44% {
      @include fontTranslate(20, -30, 4);
    }
    51%,
    100% {
      @include fontTranslate(6.1, 2.69);
    }
  }
  @keyframes fadeIn10 {
    0%,
    36% {
      @include fontTranslate(-120, -10, 10);
    }
    50% {
      @include fontTranslate(-20, -20, 4);
    }
    54%,
    100% {
      @include fontTranslate(8.13, 2.69);
    }
  }
  @keyframes fadeIn11 {
    0%,
    36% {
      @include fontTranslate(10, 100, 80);
    }
    44% {
      @include fontTranslate(5, 50, 60);
    }
    56%,
    100% {
      @include fontTranslate(0, 4.04);
    }
  }
  @keyframes fadeIn12 {
    0%,
    40% {
      @include fontTranslate(8, -100, 20);
    }
    58%,
    100% {
      @include fontTranslate(3.56, 4.04);
    }
  }
  @keyframes fadeIn13 {
    0%,
    40% {
      @include fontTranslate(-120, 2, 5);
    }
    58% {
      @include fontTranslate(-50, -2, 3);
    }
    62%,
    100% {
      @include fontTranslate(0, 5.38);
    }
  }
  @keyframes fadeIn14 {
    0%,
    58% {
      @include fontTranslate(-50, 50, 20);
    }
    64%,
    100% {
      @include fontTranslate(4.07, 5.38);
    }
  }
  @keyframes fadeIn15 {
    0%,
    62% {
      @include fontTranslate(50, 50, 20);
    }
    68%,
    100% {
      @include fontTranslate(6.61, 5.38);
    }
  }
}
