$headerHeight: 6rem;
.container {
  position: fixed;
  height: $headerHeight;
  display: flex;
  align-items: center;
  width: 100%;
  z-index: 1;
  border-bottom: 1px solid #333;
  transition: all 1s;

  &.hide {
    transform: translateY(-#{$headerHeight});
  }

  .logo {
    padding-left: 2rem;
    font-size: 3rem;
    .heightLight {
      font-weight: bold;
      color: var(--color-accent);
    }
  }
}
